'use strict';

import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

const inputDate = document.querySelector('#datetime-picker');
const startBtn = document.querySelector('button');
startBtn.setAttribute('disabled', true);

const timerDays = document.querySelector('span[data-days]');
const timerHours = document.querySelector('span[data-hours]');
const timerMinutes = document.querySelector('span[data-minutes]');
const timerSeconds = document.querySelector('span[data-seconds]');

let userSelectedDate = 0;

const options = {
  enableTime: true,
  time_24hr: true,
  defaultDate: new Date(),
  minuteIncrement: 1,
  onClose(selectedDates) {
    console.log(selectedDates[0]);
    userSelectedDate = selectedDates[0];

    if (selectedDates[0] <= options.defaultDate) {
      iziToast.show({
        title: 'Wait a minute!',
        message: 'Please choose a date in the future',
        position: 'topCenter',
        timeout: 5000,
      });
      startBtn.setAttribute('disabled', true);
    } else {
      startBtn.removeAttribute('disabled');
    }
  },
};
flatpickr('input[type="text"]', options);

function convertMs(ms) {
  // Number of milliseconds per unit of time
  const second = 1000;
  const minute = second * 60;
  const hour = minute * 60;
  const day = hour * 24;

  // Remaining days
  const days = Math.floor(ms / day);
  // Remaining hours
  const hours = Math.floor((ms % day) / hour);
  // Remaining minutes
  const minutes = Math.floor(((ms % day) % hour) / minute);
  // Remaining seconds
  const seconds = Math.floor((((ms % day) % hour) % minute) / second);

  return { days, hours, minutes, seconds };
}
function addLeadingZero(value) {
  return String(value).padStart(2, '0');
}

startBtn.addEventListener('click', () => {
  startBtn.setAttribute('disabled', true);
  inputDate.setAttribute('disabled', true);
  let leftTime = userSelectedDate - Date.now();

  let intervalId = setInterval(() => {
    leftTime -= 1000;

    timerDays.textContent = addLeadingZero(convertMs(leftTime).days);
    timerHours.textContent = addLeadingZero(convertMs(leftTime).hours);
    timerMinutes.textContent = addLeadingZero(convertMs(leftTime).minutes);
    timerSeconds.textContent = addLeadingZero(convertMs(leftTime).seconds);

    if ((leftTime / 1000 > 0 && leftTime / 1000 < 1) || leftTime / 1000 < 0) {
      clearInterval(intervalId);
      inputDate.removeAttribute('disabled');
      timerDays.textContent = '00';
      timerHours.textContent = '00';
      timerMinutes.textContent = '00';
      timerSeconds.textContent = '00';
    }
  }, 1000);
});
